import React, { useState } from "react";
import Bancario from "./Bancario";
import Racional from "./Racional";
import SecondButton from "../SecondButton";

const Simple = () => {
  const [opSelect, setOpSelect] = useState("")

  const handleSelect = (e) => {
    setOpSelect(e.target.value)
  }

  const discountTypes = (type) => {
    switch (type) {
      case "Bancario":
        return <Bancario />
      case "Racional":
        return <Racional />
      default:
        return null
    }
  }

  return (
    <div>
      <h2 className="text-center text-2xl mb-1.5">Interes Simple</h2>
      <div>
        <h3 className="text-gray-500 mb-2.5">Elija el tipo de descuento simple: </h3>
        <div className="flex justify-center mb-3">
          <SecondButton 
            event={e => handleSelect(e)}
            text='Bancario'
          />
          <SecondButton 
            event={e => handleSelect(e)}
            text='Racional'
          />
         
        </div>
        <div>{discountTypes(opSelect)}</div>
      </div>
    </div>
  )
}

export default Simple;
