import React from 'react';
import Input from './Input';

const FormOne = (props) => {
  return (
    <>
      <form onSubmit={props.operacion}>
        <Input 
          tag={props.name1}
          event={props.event1}
          text={props.text1}
          formato={props.forma1}
        />
        <Input 
          tag={props.name2}
          event={props.event2}
          text={props.text2}
          formato={props.forma2}
        />
        <Input 
          tag={props.name3}
          event={props.event3}
          text={props.text3}
          formato={props.forma3}
        />
        <div>
          <button className="px-4 py-1 rounded-lg bg-red-500 text-white font-semibold uppercase text-sm outline-none border-none focus:outline-none"
            type="submit"
            >Calcular
          </button>
        </div>
      </form>
      <div>
        {
          props.resultado
            ? <p className="text-xl mt-3.5">El {props.select} es: <span className="text-green-600">{props.resultado}</span></p>
            : null
        }
      </div>
    </>
  );
}
 
export default FormOne;