import React, { useState } from "react";
import FormOne from "../FormOne";
import FormThree from "../FormThree";

const TasasSelect= (props) => {
  return (
    <>
      <span>Hallar : </span>
      <div className="inline-block relative w-35 mb-3 ml-1">
        <select className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 px-2 py-1 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline"
          onChange={props.onChange}
        >
          <option value="elegir">- Elija uno -</option>
          <option value={props.op1}>{props.op1}</option>
          <option value={props.op2}>{props.op2}</option>
        </select>
        <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
          <svg className="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
        </div>
      </div>
      <div>{props.function}</div>
    </>
  )
}

const Bancario = () => {
  const [valorNominal, setValorNominal] = useState('')
  const [valorLiquido, setValorLiquido] = useState('')
  const [periodos, setPeriodos] = useState('')
  const [tasaDescuento, setTasaDescuento] = useState('')
  const [interesSimple, setInteresSimple] = useState('')
  const [resultado, setResultado] = useState('')
  const [calcular, setCalcular] = useState(['- Elija una opcíón -',
  'Descuento',
  'Valor líquido',
  'Valor nominal',
  'Plazo n',
  'Equivalencia de tasas'])
  const [select, setSelect] = useState('')
  const [tasaSelect, setTasaSelect] = useState('')

  const Opts = calcular.map(op => op);

  //Funciones para los calculos
  const calcDescuento = (e) => {
    e.preventDefault()

    let F = parseFloat(valorNominal);
    let n = parseFloat(periodos);
    let d = parseFloat(tasaDescuento);
    let result

    result = F * (1 - Math.pow((1 - d), n))

    setResultado(result.toFixed(3).toString())
  }

  const calcValorLiquido = (e) => {
    e.preventDefault()

    let F = parseFloat(valorNominal);
    let n = parseFloat(periodos);
    let d = parseFloat(tasaDescuento);
    let result

    result = F * Math.pow((1 - d),n)

    setResultado(result.toFixed(3).toString())
  }

  const calcValorNominal = (e) => {
    e.preventDefault()

    let P = parseFloat(valorLiquido);
    let n = parseFloat(periodos);
    let d = parseFloat(tasaDescuento);
    let result

    result = P * Math.pow((1 - d), (-1 * n))

    setResultado(result.toFixed(2).toString())
  }

  const calcPlazoN = (e) => {
    e.preventDefault()

    let P = parseFloat(valorLiquido)
    let F = parseFloat(valorNominal)
    let d = parseFloat(tasaDescuento)
    let result

    const logBase10 = (x, y) => (
      Math.log(x) / Math.log(y)
    )

    
    result = logBase10(10, (P / F)) / logBase10(10, (1 - d))
    setResultado(result.toFixed(2).toString())
  }

  const calcInteresSimple = (e) => {
    e.preventDefault()

    let d = parseFloat(tasaDescuento)
    let result

    result = d / (1 - d)

    setResultado(result.toFixed(3).toString())
  }

  const calcTasaDescuento = (e) => {
    e.preventDefault()

    let i = parseFloat(interesSimple)
    let result
    
    result = i / (1 + i)

    setResultado(result.toFixed(3).toString())
  }

  //Manejadores de eventos
  const handleValorNominal = (e) => {
    setValorNominal(e.target.value)
  }

  const handleValorLiquido = (e) => {
    setValorLiquido(e.target.value)
  }
  
  const handleInteresSimple = (e) => {
    setInteresSimple(e.target.value)
  }

  const handlePeriodos = (e) => {
    setPeriodos(e.target.value)
  }

  const handleTasaDescuento = (e) => {
    setTasaDescuento(e.target.value);
  }

  const handleSelect = (e) => {
    setSelect(calcular[e.target.value])
  }

  const handleTasaSelect = (e) => {
    setTasaSelect(e.target.value)
  }

  //Seleccionadores de calculos
  const switchTasa = (opcion) => {
    switch (opcion) {
      case 'Tasa de descuento':
        return <FormThree 
                operacion={calcTasaDescuento}
                name='Interes Simple'
                event={e => handleInteresSimple(e)}
                text={interesSimple}
                forma='0.00'
                message='La tasa equivalente es: '
                resultado={resultado}
              />
      case 'Interes Simple':
        return <FormThree 
                operacion={calcInteresSimple}
                name='Tasa de Descuento'
                event={e => handleTasaDescuento(e)}
                text={tasaDescuento}
                forma='0.00'
                message='La tasa equivalente es: '
                resultado={resultado}
              />
      default:
        return null
    }
  }
  const elegirCalculo = (op) => {
    switch(op){
      case 'Descuento':
        return <FormOne 
                operacion={calcDescuento}
                name1='Valor nominal'
                event1={e => handleValorNominal(e)}
                text1={valorNominal}
                forma1={null}
                name2='Periodos'
                event2={e => handlePeriodos(e)}
                text2={periodos}
                forma2={null}
                name3='Tasa de Descuento'
                event3={handleTasaDescuento}
                text3={tasaDescuento}
                forma3='0.00'
                resultado={resultado}
                select={select}
              />
      case 'Valor líquido':
        return <FormOne 
                operacion={calcValorLiquido}
                name1='Valor nominal'
                event1={e => handleValorNominal(e)}
                text1={valorNominal}
                forma1={null}
                name2='Periodos'
                event2={e => handlePeriodos(e)}
                text2={periodos}
                forma2={null}
                name3='Tasa de Descuento'
                event3={handleTasaDescuento}
                text3={tasaDescuento}
                forma3='0.00'
                resultado={resultado}
                select={select}
              />
      case 'Valor nominal':
        return <FormOne 
                operacion={calcValorNominal}
                name1='Valor Líquido'
                event1={e => handleValorLiquido(e)}
                text1={valorLiquido}
                forma1={null}
                name2='Periodos'
                event2={e => handlePeriodos(e)}
                text2={periodos}
                forma2={null}
                name3='Tasa de Descuento'
                event3={handleTasaDescuento}
                text3={tasaDescuento}
                forma3='0.00'
                resultado={resultado}
                select={select}
              />
      case 'Plazo n':
        return <FormOne
                operacion={calcPlazoN}
                name1='Valor Líquido'
                event1={e => handleValorLiquido(e)}
                text1={valorLiquido}
                forma1={null}
                name2='Valor Nominal'
                event2={e => handleValorNominal(e)}
                text2={valorNominal}
                forma2={null}
                name3='Tasa de Descuento'
                event3={handleTasaDescuento}
                text3={tasaDescuento}
                forma3='0.00'
                resultado={resultado}
                select={select}
              />
      case 'Equivalencia de tasas':
        return <TasasSelect 
                onChange={e => handleTasaSelect(e)}
                op1='Tasa de descuento'
                op2='Interes Simple'
                function={switchTasa(tasaSelect)}
              />
      default: 
        return null
    }
  }

  return (
    <>
      <h3 className="text-gray-500 mb-3">¿Qué desea calcular?</h3>
      <div className="inline-block relative w-64 mb-3">
        <select className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline "
          onChange={e => handleSelect(e)}>
          {
            Opts.map((op, key) => (
              <option key={key} value={key}>
                {op}
              </option>
            ))
          }
        </select>
        <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
          <svg className="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
        </div>
      </div>
      <div>{elegirCalculo(select)}
      </div>
    </>
  )
}

export default Bancario;