import React from 'react';
import Input from './Input';

const FormTwo = (props) => {
  return (
    <>
      <form onSubmit={props.calculo}>
        <Input 
          tag={props.name1}
          event={props.event1}
          text={props.text1}
          formato={props.forma1}
        />
        <Input 
          tag={props.name2}
          event={props.event2}
          text={props.text2}
          formato={props.forma2}
        />
        <div>
          <button className="px-4 py-1 rounded-lg bg-red-500 text-white font-semibold uppercase text-sm outline-none border-none focus:outline-none"
            type="submit"
            >Calcular
          </button>
        </div>
        <div>
        {
          props.resultado
            ? <p className="text-xl mt-3.5">{props.message}<span className="text-green-600">{props.resultado}</span></p>
            : null
        }
      </div>
      </form>
    </>
  );
}
 
export default FormTwo;