import React from 'react';

const Input = (props) => {
  return (
    <div className="font-medium">
      {props.tag} : 
      <input className="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 mb-4"
        onChange={props.event} 
        value={props.text}
        required
        placeholder={props.formato} 
      /> 
    </div>
  );
}
 
export default Input;

