import React from 'react';

const MainButton = (props) => {
  return (
    <button className="px-6 py-2 rounded-lg bg-green-400 text-white font-semibold uppercase text-sm outline-none border-none focus:outline-none focus:ring-2 focus:ring-green-300"
      onClick={props.event}
      value={props.text}
    >{props.text}</button>
  )
}

export default MainButton;