import React, {useState} from 'react';
import Simple from './components/Simple/Simple';
import Compuesto from './components/Compuesto/Compuesto';
import MainButton from './components/MainButton';

function App() {
  const [selected, setSelected] = useState('');
  
  const opSelect = (e) => {
    setSelected(e.target.value)
    // console.log(e.target.value)
  }

  const showForm = option => {
    switch(option){
      case 'Simple':
        return <Simple />
      case 'Compuesto':
        return <Compuesto />
      default:
        return null
    }
  }

  return (
    <div className="flex justify-center h-screen bg-gray-300">
      <div className="m-auto rounded-3xl overflow-hidden shadow-lg p-5 bg-white">
        <div>
          <h1 className="text-center font-bold text-3xl mb-2 py-4">Calculadora de Descuentos</h1>
          <div className="grid grid-cols-2 gap-x-2 mb-3">
            <MainButton 
              event={e => opSelect(e)}
              text='Simple'
            />
            <MainButton 
              event={e => opSelect(e)}
              text='Compuesto'
            />
          </div>
          <div>{showForm(selected)}
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
