import React, {useState} from "react";
import FormOne from "../FormOne";
import FormTwo from "../FormTwo";

const Calculo = (props) => {
  return (
    <>
      <span>Hallar {props.name} usando: </span>
      <div className="inline-block relative w-35 mb-3 ml-1">
        <select className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 px-2 py-1 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline"
          onChange={props.onChange}
        >
          <option value="elegir">- Elija uno -</option>
          <option value={props.op1}>{props.op1}</option>
          <option value={props.op2}>{props.op2}</option>
        </select>
        <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
          <svg className="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
        </div>
      </div>
      <div>{props.function}</div>
    </>
  )
}

const Racional = () => {
  const [opSelected, setOpSelected] = useState("")
  const [discountType, setDiscountType] = useState("")
  const [equivalencia, setEquivalencia] = useState("")
  const [valorLiquido, setValorLiquido] = useState("")
  const [valorFuturo, setValorFuturo] = useState("")
  const [periodos, setPeriodos] = useState("")
  const [interesSimple, setInteresSimple] = useState("")
  const [tasaDescuento, setTasaDescuento] = useState("")
  const [resultado, setResultado] = useState("")

  //Select General
  const handleOpSelect = (e) => {
    setOpSelected(e.target.value)
  }

  //Select de cada opcion
  const handleOp1 = (e) => {
    setDiscountType(e.target.value)
  }

  const hadleOp2 = (e) => {
    setEquivalencia(e.target.value)
  }

  //Inputs de cada tipo de calculo
  const handleValorLiquido = (e) => {
    setValorLiquido(e.target.value)
  }

  const hanldeValorFuturo = (e) => {
    setValorFuturo(e.target.value)
  }

  const handlePeriodos = (e) => {
    setPeriodos(e.target.value)
  }

  const handleInteresSimple = (e) => {
    setInteresSimple(e.target.value)
  }

  const handleTasaDescuento = (e) => {
    setTasaDescuento(e.target.value)
  }


  //Funciones para obtener el resultado de cada tipo de calculo
  const desRacValorLiquido = (e) => {
    e.preventDefault()

    let S = parseFloat(valorLiquido)
    let n = parseInt(periodos)
    let i = parseFloat(interesSimple)
    let result

    result = (S * n * i) / (1 + n * i)

    setResultado(result.toFixed(3).toString())
  }

  const desRacValorFuturo = (e) => {
    e.preventDefault()

    let C = parseFloat(valorFuturo)
    let n = parseInt(periodos)
    let i = parseFloat(interesSimple)
    let result

    result = C * n * i

    setResultado(result.toFixed(3).toString())
  }
  
  const tasaIntSimple = (e) => {
    e.preventDefault()

    let n = parseInt(periodos)
    let d = parseFloat(tasaDescuento)
    let result 

    result = d / (1 - n * d)

    setResultado(result.toFixed(3).toString())
  }

  const tasaDescuentoRacional = (e) => {
    e.preventDefault()

    let n = parseInt(periodos)
    let i = parseFloat(interesSimple)
    let result

    result = i / (1 + n * i)

    setResultado(result.toFixed(3).toString())
  }
  
  //Switch de opciones del DesCuento Racional
  const switchDescRacional = (op) => {
    switch(op){
      case 'Valor liquido':
        return <FormOne
                operacion={desRacValorLiquido}
                name1='Valor liquido'
                event1={e => handleValorLiquido(e)}
                text1={valorLiquido}
                forma1={null}
                name2='Periodos'
                event2={e => handlePeriodos(e)}
                text2={periodos}
                forma2={null}
                name3='Interes simple'
                event3={e => handleInteresSimple(e)}
                text3={interesSimple}
                forma3='0.00'
                resultado={resultado}
                select={opSelected}
              />
      case 'Valor futuro':
        return <FormOne
                operacion={desRacValorFuturo}
                name1='Valor futuro'
                event1={e => hanldeValorFuturo(e)}
                text1={valorFuturo}
                forma1={null}
                name2='Periodos'
                event2={e => handlePeriodos(e)}
                text2={periodos}
                forma2={null}
                name3='Interes simple'
                event3={e => handleInteresSimple(e)}
                text3={interesSimple}
                forma3='0.00'
                resultado={resultado}
                select={opSelected}
              />
      default:
        return null
    }
  }

  const switchTasas = (tipo) => {
    switch (tipo) {
      case 'Interes simple':
        return <FormTwo 
                calculo={tasaDescuentoRacional}
                name1='Periodos'
                event1={e => handlePeriodos(e)}
                text1={periodos}
                forma1={null}
                name2='Interes simple'
                event2={e => handleInteresSimple(e)}
                text2={interesSimple}
                forma2='0.00'
                message='La tasa equivalente es: '
                resultado={resultado}
              />
      case 'Tasa de descuento':
        return <FormTwo 
                calculo={tasaIntSimple}
                name1='Periodos'
                event1={e => handlePeriodos(e)}
                text1={periodos}
                forma1={null}
                name2='Tasa de descuento'
                event2={e => handleTasaDescuento(e)}  
                text2={tasaDescuento}
                forma2='0.00'
                message='La tasa equivalente es: '
                resultado={resultado}
              />
      default:
        return null
    }
  }

  const showForm = (op) => {
    switch(op){
      case 'descRacional':
        return <Calculo 
                name='descuento racional'
                op1='Valor liquido'
                op2='Valor futuro'
                onChange={e => handleOp1(e)}
                function={switchDescRacional(discountType)}
              />
      case 'equivalencia':
        return <Calculo 
                name='equivalencia de tasas'
                op1='Interes simple'
                op2='Tasa de descuento'
                onChange={e => hadleOp2(e)}
                function={switchTasas(equivalencia)}
              />
      default:
        return null
    }
  }

  return (
    <>
      <h3 className="text-gray-500 mb-3">¿Qué desea calcular?</h3>
      <div className="inline-block relative w-64 mb-3">
        <select className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline" required
          onChange={e => handleOpSelect(e)}
          value={opSelected}
        >
          <option >
            - Elija una opción -
          </option>
          <option value="descRacional">
            Descuento Racional
          </option>
          <option value="equivalencia">
            Equivalencia de tasas
          </option>
        </select>
        <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
          <svg className="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
        </div>
      </div>
      <div>{showForm(opSelected)}</div>
    </>
  )
}

export default Racional;
