import React from 'react';

const SecondButton = (props) => {
  return (  
    <button className="px-4 py-1 rounded-lg bg-green-700 text-white font-semibold uppercase text-sm outline-none border-none focus:outline-none mx-1.5 focus:ring-2 focus:ring-green-600"
      onClick={props.event}
      value={props.text}
    >{props.text}</button>
  );
}
 
export default SecondButton;