import React from 'react';
import Input from './Input';

const FormThree = (props) => {
  return (
    <>
      <form onSubmit={props.operacion}>
        <Input 
          tag={props.name}
          event={props.event}
          valor={props.text}
          formato={props.forma}
        />
        <div>
          <button className="px-4 py-1 rounded-lg bg-red-500 text-white font-semibold uppercase text-sm outline-none border-none focus:outline-none"
            type="submit"
            >Calcular
          </button>
        </div>
      </form>
      <div>
        {
          props.resultado
            ? <p className="text-xl mt-3.5">{props.message}<span className="text-green-600">{props.resultado}</span></p>
            : null
        }
      </div>
    </>
  );
}
 
export default FormThree;